/* EligibilityTool.css */

/* Global styles for the entire app */

/* Apply some basic styling to the entire page */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  color: #ffffff; /* Ensuring text color consistency */
}

/* Style the header */
.App-header {
  background-color: #02ab9d; /* Header background color */
  color: white; /* Header text color */
  padding: 20px;
  text-align: center;
  font-size: 24px;
  font-weight: bold;
}

/* Style the main content */
main {
  padding: 20px;
  text-align: left; /* Align text to the left */
}

/* Style the main container */
.App {
  text-align: left; /* Align text to the left */
}
